import { CircleDefinition } from './CirlceDataTypes';
import { OwnerProviderBase } from '../OwnerProviderBase';

//Handles management of Circles
export class CircleProvider extends OwnerProviderBase {
  private static instance: CircleProvider;
  private root = '/circles/definitions';

  constructor(sharedSecret: Uint8Array | undefined) {
    if (!sharedSecret) {
      throw 'Shared Secret is required';
    }
    super(sharedSecret);
  }

  async updateDefinition(circleDefinition: CircleDefinition) {
    const client = super.createAxiosClient();
    const url = this.root + '/update';

    const data = { ...circleDefinition };
    data.drivesGrants = data.drivesGrants || data.drives;
    data.drives = undefined;
    console.log('actual update data:', data);

    return client
      .post(url, data)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async createDefinition(circleDefinition: CircleDefinition) {
    const client = super.createAxiosClient();
    const url = this.root + '/create';

    return client
      .post(url, circleDefinition)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async getCircles(): Promise<CircleDefinition[]> {
    const client = super.createAxiosClient();
    const url = this.root + '/list';

    return client
      .get(url)
      .then((response) => {
        return response.data.map((circleDef) => {
          circleDef.drivesGrants = circleDef.drivesGrants?.map((grant) => {
            return {
              ...grant,
              permission:
                grant.permission === 'Read'
                  ? 2
                  : grant.permission === 'Write'
                  ? 4
                  : grant.permission === 'ReadWrite'
                  ? 5
                  : grant.permission,
            };
          });

          return circleDef;
        });
      })
      .catch(super.handleErrorResponse);
  }

  async getCircle(circleId: string): Promise<CircleDefinition> {
    const client = super.createAxiosClient();
    const url = this.root + '/circle';

    return client
      .post(url, circleId)
      .then((response) => {
        return response.data;
      })
      .then((circleDef) => {
        // Parse string permissionflags to number representations
        circleDef.drivesGrants = circleDef.drivesGrants?.map((grant) => {
          return {
            ...grant,
            permission:
              grant.permission === 'Read'
                ? 2
                : grant.permission === 'Write'
                ? 4
                : grant.permission === 'ReadWrite'
                ? 5
                : grant.permission,
          };
        });

        // const globalPermission = circleDef.permissions.permissions;
        // circleDef.permissions.permissions =
        //   globalPermission === 'ApproveConnection'
        //     ? 1
        //     : globalPermission === 'ReadConnections'
        //     ? 2
        //     : globalPermission === 'ApproveConnection, ReadConnections'
        //     ? 3
        //     : globalPermission === 'ApproveConnection, ReadConnections, UpdateConnections'
        //     ? 7
        //     : globalPermission ===
        //       'ApproveConnection, ReadConnections, UpdateConnections, DeleteConnections'
        //     ? 15
        //     : globalPermission;
        return circleDef;
      })
      .catch(super.handleErrorResponse);
  }

  public static getInstance(sharedSecret): CircleProvider {
    if (!CircleProvider.instance) {
      CircleProvider.instance = new CircleProvider(sharedSecret);
    }

    return CircleProvider.instance;
  }
}
