import { FC, ReactNode } from 'react';
import Arrow from '../Icons/Arrow/Arrow';
import Check from '../Icons/Check/Check';
import Exclamation from '../Icons/Exclamation/Exclamation';
import Loader from '../Icons/Loader/Loader';
import Plus from '../Icons/Plus/Plus';
import Save from '../Icons/Save/Save';
import Trash from '../Icons/Trash/Trash';

export type ActionButtonState = 'loading' | 'success' | 'error' | 'idle';

type ActionButtonProps = {
  children?: ReactNode;
  className?: string;
  icon?: 'save' | 'send' | 'plus' | 'trash';
  type?: 'primary' | 'secondary' | 'remove';
  state?: ActionButtonState;
  onClick?: React.MouseEventHandler<HTMLButtonElement>;
  title?: string;
  size?: 'large' | 'small';
};

export const mergeStates = (
  stateA: ActionButtonState,
  stateB: ActionButtonState
): ActionButtonState => {
  if (stateA === 'error' || stateB === 'error') {
    return 'error';
  }

  if (stateA === 'loading' || stateB === 'loading') {
    return 'loading';
  }

  if (stateA === 'idle' && stateB === 'idle') {
    return 'idle';
  }

  if (stateA === 'success' && stateB === 'success') {
    return 'success';
  }

  if ((stateA === 'success' && stateB === 'idle') || (stateA === 'idle' && stateB === 'success')) {
    return 'success';
  }
};

const ActionButton: FC<ActionButtonProps> = ({
  children,
  onClick,
  className,
  icon,
  type,
  state,
  title,
  size,
}) => {
  const Icon = (props: { className: string }) => {
    if (state === 'loading') {
      return <Loader {...props} />;
    }
    if (state === 'success') {
      return <Check {...props} />;
    }
    if (state === 'error') {
      return <Exclamation {...props} />;
    }

    return icon === 'save' ? (
      <Save {...props} />
    ) : icon === 'send' ? (
      <Arrow {...props} />
    ) : icon === 'plus' ? (
      <Plus {...props} />
    ) : icon === 'trash' ? (
      <Trash {...props} />
    ) : (
      <></>
    );
  };

  const colorClasses =
    state === 'error'
      ? 'bg-red-500 hover:bg-red-600 text-white'
      : type === 'secondary'
      ? 'bg-slate-100 hover:bg-slate-200 dark:bg-slate-700 dark:hover:bg-slate-800 dark:text-white'
      : type === 'remove'
      ? 'bg-red-200 hover:bg-red-400 dark:bg-red-700 hover:dark:bg-red-800 dark:text-white'
      : 'bg-green-500 hover:bg-green-600 text-white';

  const sizeClasses =
    size === 'large' ? 'px-5 py-3' : size === 'small' ? 'px-3 py-1 text-sm' : 'px-3 py-2';

  return (
    <button
      className={`flex ${
        children ? 'min-w-[6rem]' : ''
      } flex-row rounded-md ${sizeClasses} text-left ${colorClasses}  ${
        state === 'loading' ? 'animate-pulse' : ''
      } ${className}`}
      disabled={state === 'loading'}
      onClick={onClick}
      title={title}
    >
      <span className="mr-1">{children}</span>
      <Icon className={'my-auto ml-auto h-4 w-4'} />
    </button>
  );
};

export default ActionButton;
