import { ConnectionInfo, DotYouIdRequest, DotYouProfile } from './CirlceDataTypes';
import { PagedResult, PagingOptions } from '@youfoundation/transit-lib';
import { OwnerProviderBase } from '../OwnerProviderBase';
import { stringify } from '../../helpers/common';

//Handles management of established connections
export class CircleNetworkProvider extends OwnerProviderBase {
  private static instance: CircleNetworkProvider;
  private root = '/circles/connections';

  constructor(sharedSecret: Uint8Array | undefined) {
    if (!sharedSecret) {
      throw 'Shared Secret is required';
    }
    super(sharedSecret);
  }

  async blockDotYouId(dotYouId: string): Promise<boolean> {
    const client = super.createAxiosClient();
    const url = this.root + '/block';

    const data: DotYouIdRequest = { dotYouId: dotYouId };
    return client
      .post(url, data)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async disconnectFromContact(dotYouId: string): Promise<boolean> {
    const client = super.createAxiosClient();
    const url = this.root + '/disconnect';
    const data: DotYouIdRequest = { dotYouId: dotYouId };
    return client
      .post(url, data)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async getConnections(data: PagingOptions): Promise<PagedResult<DotYouProfile>> {
    const client = super.createAxiosClient();
    const url = this.root + '/connected?' + stringify(data);
    return client.post(url, data).then((response) => {
      return response.data;
    });
  }

  async getBlockedConnections(params: PagingOptions): Promise<PagedResult<DotYouProfile>> {
    const client = super.createAxiosClient();
    const url = this.root + '/blocked?' + stringify(params);
    return client
      .get(url)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  getConnectionInfo(dotYouId: string): Promise<ConnectionInfo> {
    const client = super.createAxiosClient();
    const url = this.root + '/status';

    const data: DotYouIdRequest = { dotYouId: dotYouId };
    return client
      .post(url, data)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }
  public static getInstance(sharedSecret): CircleNetworkProvider {
    if (!CircleNetworkProvider.instance) {
      CircleNetworkProvider.instance = new CircleNetworkProvider(sharedSecret);
    }

    return CircleNetworkProvider.instance;
  }
}
