import { OwnerProviderBase } from '../OwnerProviderBase';

//Handles management of Circles
export class CircleMembershipProvider extends OwnerProviderBase {
  private static instance: CircleMembershipProvider;
  private root = '/circles/connections/circles';

  constructor(sharedSecret: Uint8Array | undefined) {
    if (!sharedSecret) {
      throw 'Shared Secret is required';
    }
    super(sharedSecret);
  }

  async add(membershipGrant: { dotYouId: string; circleId: string }) {
    const client = super.createAxiosClient();
    const url = this.root + '/add';

    return client
      .post(url, membershipGrant)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async remove(membershipGrant: { dotYouId: string; circleId: string }) {
    const client = super.createAxiosClient();
    const url = this.root + '/revoke';

    return client
      .post(url, membershipGrant)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async fetchMembers(circleId: string): Promise<string[]> {
    const client = super.createAxiosClient();
    const url = this.root + '/list';

    return client
      .post(url, { circleId: circleId })
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  public static getInstance(sharedSecret): CircleMembershipProvider {
    if (!CircleMembershipProvider.instance) {
      CircleMembershipProvider.instance = new CircleMembershipProvider(sharedSecret);
    }

    return CircleMembershipProvider.instance;
  }
}
