import {
  AcceptRequestHeader,
  ConnectionRequest,
  ConnectionRequestHeader,
  DotYouIdRequest,
} from './CirlceDataTypes';
import { PagedResult, PagingOptions } from '@youfoundation/transit-lib';
import { OwnerProviderBase } from '../OwnerProviderBase';
import { stringify } from '../../helpers/common';

//Handles making and reading requests to connect with others
export class CircleNetworkRequestProvider extends OwnerProviderBase {
  private static instance: CircleNetworkRequestProvider;

  private Root = '/circles/requests';
  private SentPathRoot: string = this.Root + '/sent';
  private PendingPathRoot: string = this.Root + '/pending';

  constructor(sharedSecret: Uint8Array | undefined) {
    if (!sharedSecret) {
      throw 'Shared Secret is required';
    }
    super(sharedSecret);
  }

  async getPendingRequests(params: PagingOptions): Promise<PagedResult<ConnectionRequest>> {
    const client = super.createAxiosClient();
    const url = this.PendingPathRoot + '/list?' + stringify(params);

    return client
      .get(url)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async getPendingRequest(dotYouId: string): Promise<ConnectionRequest> {
    const client = super.createAxiosClient();
    const url = this.PendingPathRoot + '/single';

    //TODO: encrypt payload with shared secret when server requires it
    const data: DotYouIdRequest = { dotYouId: dotYouId };
    return client
      .post(url, data)
      .then((response) => {
        return { ...response.data, status: 'Pending' };
      })
      .catch(() => {
        return undefined;
      });
  }

  async getSentRequests(params: PagingOptions): Promise<PagedResult<ConnectionRequest>> {
    const client = super.createAxiosClient();
    const url = this.SentPathRoot + '/list?' + stringify(params);

    return client
      .get(url)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async getSentRequest(dotYouId: string): Promise<ConnectionRequest> {
    const client = super.createAxiosClient();
    const url = this.SentPathRoot + '/single';

    //TODO: encrypt payload with shared secret when server requires it
    const data: DotYouIdRequest = { dotYouId: dotYouId };

    return client
      .post(url, data)
      .then((response) => {
        return { ...response.data, status: 'Sent' };
      })
      .catch((err) => {
        if (err.response.status === 404) {
          return null;
        }
        super.handleErrorResponse(err);
      });
  }

  async acceptConnectionRequest(dotYouId: string): Promise<boolean> {
    const client = super.createAxiosClient();
    const url = this.PendingPathRoot + '/accept/';

    //todo: build from drives a UI selection of drives
    // let drive: DriveGrantRequest = {
    //     drive: {
    //         alias: "",
    //         type: ""
    //     },
    //     permission: DrivePermissions.None
    // }

    const header: AcceptRequestHeader = {
      sender: dotYouId,
      drives: [],
      permissions: null,
    };

    return client
      .post(url, header)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async deletePendingRequest(dotYouId: string): Promise<boolean> {
    const client = super.createAxiosClient();
    const url = this.PendingPathRoot + '/delete';

    //TODO: encrypt payload with shared secret when server requires it
    const data: DotYouIdRequest = { dotYouId: dotYouId };

    return client
      .post(url, data)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async deleteSentRequest(dotYouId: string): Promise<boolean> {
    const client = super.createAxiosClient();
    const url = this.SentPathRoot + '/delete';
    //TODO: encrypt payload with shared secret when server requires it
    const data: DotYouIdRequest = { dotYouId: dotYouId };

    return client
      .post(url, data)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  async sendRequest(
    dotYouId: string,
    message: string,
    name: string,
    circleIds: string[]
  ): Promise<boolean> {
    const url = this.Root + '/sendrequest';
    const data: ConnectionRequestHeader = {
      recipient: dotYouId,
      message: message,
      name: name,
      circleIds: circleIds,
    };

    const client = super.createAxiosClient();
    return client
      .post(url, data)
      .then((response) => {
        return response.data;
      })
      .catch(super.handleErrorResponse);
  }

  public static getInstance(sharedSecret): CircleNetworkRequestProvider {
    if (!CircleNetworkRequestProvider.instance) {
      CircleNetworkRequestProvider.instance = new CircleNetworkRequestProvider(sharedSecret);
    }

    return CircleNetworkRequestProvider.instance;
  }
}
